body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a, a:hover {
  text-decoration: none;
  color: #d0021b;
}

.nav {
  margin-bottom: 15px;
  padding: 15px;
  border-bottom: 1px black solid;
}

.active {
  font-weight: bold;
}

.nav li {
  margin-right: 15px;
}


/* General commonly used styles */
.border {
  border-width: 1px;
  border-style: solid;
}

.red {
  color: #d0021b;
}

/* Store */
.container-store {
  padding: 15px 15px;
}

#review-text {
  margin-bottom: 15px;
}

#review-container {
  height: 500px;
}

.store-front-links {
  height: 30px;
}

/* Store Rating */
.rating {
  display: inline-block;
}

.rating:not(:checked) > div > input {
  position:absolute;
  top:-9999px;
  clip:rect(0,0,0,0);
}

.rating:not(:checked) > div > label {
  float:right;
  width:1.28em;
  margin: 0px 0.25em;
  overflow:hidden;
  white-space:nowrap;
  cursor:pointer;
  font-size:1em;
  line-height:1.2;
  color:#ddd;
  text-shadow:1px 1px #bbb, 2px 2px #666, .1em .1em .2em rgba(0,0,0,.5);
}

.rating:not(:checked) > div > label:before {
  content: 'â˜… ';
}

.rating > div > input:checked ~ label {
  color: #f70;
  text-shadow:1px 1px #c60, 2px 2px #940, .1em .1em .2em rgba(0,0,0,.5);
}

.rating:not(:checked) > div > label:hover,
.rating:not(:checked) > div > label:hover ~ label {
  color: gold;
  text-shadow:1px 1px goldenrod, 2px 2px #B57340, .1em .1em .2em rgba(0,0,0,.5);
}

.rating > div > input:checked + label:hover,
.rating > div > input:checked + label:hover ~ label,
.rating > div > input:checked ~ label:hover,
.rating > div > input:checked ~ label:hover ~ label,
.rating > label:hover ~ input:checked ~ label {
  color: #ea0;
  text-shadow:1px 1px goldenrod, 2px 2px #B57340, .1em .1em .2em rgba(0,0,0,.5);
}

.rating > div > label:active {
  position:relative;
  top:2px;
  left:2px;
}

.store-front-star {
  pointer-events: none;
}

/* Store Map */
.store-map {
  width: 100%;
  height: 600px;
  padding: 5px;
}

.map {
  width: 200px;
  height: 200px;
}

/* Order */
.order {
  min-height: 400px;
}


/* Seller Admin */
.stamp-container{
  width: 100px;
}

.stamp{
  color: red;
  border-color: red;
  padding: 5px;
  left:50px;
  top:10px;
  border-radius:5px;
  position:relative;
  background-color: white;
  -webkit-animation-name: pulse;
  animation-name: pulse;
  animation-delay:0.5s;
  -webkit-animation-delay:0.5s;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  opacity: 0;
  -webkit-animation-fill-mode:forwards;
   animation-fill-mode:forwards;
}

@-webkit-keyframes pulse{
  0%{
    color: red;
    border-color: red;
    opacity: 0;
  }
  10%{
    color: red;
    border-color: red;
    opacity:.50;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(-2deg) scale(5);
            transform: rotate(-2deg) scale(5);
    -webkit-transition: all .3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    -o-transition: all .3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    transition: all .3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  }
  100%{
    opacity:1;
    -webkit-transform: rotate(-15deg) scale(1);
            transform: rotate(-15deg) scale(1);
    color: red;
    border-color: red;
  }
}






